.shadow {
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0000008a;
}

.gameOver {
    padding: 1vw 1vw;
    font-size: 2rem;
    background-color: #c70707;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 80%;
}